import { apply,upload,discernID,userRegister,login,sendSms,isSubbranch } from "@/api/login";
import { Toast } from 'vant';
export default {
  data() {
    return {
      password:"",
      account:"",
              checked: true,
  current: 0,
               minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate:false,
      b:true,
