      slides: [],
      goodsValue: 1,
      goodsId: "",
      goodsInfo: {},
      freight: "",
      deposit:"",
      get:"",
      decript:"",
      loan:"",
      use_loan:"",
      bank_type:"",
      backlist:[],
      goodsSize: [],
      list:[],
      chooseSize: {},
       show: false,
       cbd: false,
       show1: false,
       show2: false,
       id:'',
      actions: [
        { name: '营业部' },
        { name: '新城支行' },
        { name: '西区支行' },
        { name: '城关支行' },
        { name: '铁门支行' },
        { name: '李村支行' },
        { name: '磁涧支行' },
        { name: '五头支行' },
        { name: '正村支行' },
        { name: '仓头支行' },
        { name: '石井支行' },
        { name: '北冶支行' },
        { name: '石寺支行' },
        { name: '青要山支行' },
        { name: '高平分理处' },
      ],
       jear: [
        { name: '10万以内' },
        { name: '10万以上30万以内' },
        { name: '30万以上' },
      ],
        ytar: [
        { name: '经营类' },
        { name: '消费类' },
        { name: '其他' },
      ],
