    }
  },
  created() {
  
  },
  methods: {
        countDown(time) {
      if (time === 0) {
        this.disabled = true;
        this.codetext = "获取";
        return;
      } else {
        this.disabled = false;
        this.codetext = "重新发送(" + time + ")";
        time--;
      }
      setTimeout(() => {
        this.countDown(time);
      }, 1000);
    },
       send() {
      sendSms({ phone: this.account }).then(res => {
        this.countDown(60);
      });
    },
        getBase64(file) {
      return new Promise(function(resolve, reject) {
        let reader = new FileReader();
        let imgResult = "";
        reader.readAsDataURL(file);
        reader.onload = function() {
          imgResult = reader.result;
        };
        reader.onerror = function(error) {
          reject(error);
        };
        reader.onloadend = function() {
          resolve(imgResult);
        };
      });
    },
      upImg(file) {
      //   console.log(file)
      //      this.getBase64(file.file).then(res => {
      //   let isJPG = res.split(";")[0].split("/")[1];
      //   if (isJPG == "jpeg" || isJPG == "jpg" || isJPG == "png") {
      //     this.image = res.split(",")[1];
      //     console.log(this.image,123,res)
      //           let data={
      //   img:this.image
      // }
      //     upload(this.image, "goods").then(resp => {
         
      //     });
      //   } else {
      //     this.$message.error("上传图片只能是正确的图片格式!");
      //     return;
      //   }
      // });
      this.image = file.content.substring(file.content.indexOf(",") + 1);
          let data={
        img:this.image
      }
      upload(data).then(resp => {
        this.img = resp.img;
        let dat={
          path:'../imgs/11.jpg'
        }
        discernID(dat).then(res=>{
             this.idcard=res.idcard
             this.name=res.name
             this.address=res.address
        })
      });
