       this.endTimePop=false
    },
     formatter(type, val) {
      if (type === 'year') {
        return `${val}年`;
      } else if (type === 'month') {
        return `${val}月`
      }else if (type === 'day') {
        return `${val}日`
      }
      return val;
    },
    endTimePo(){
if(this.abcd==1){
       this.endTimePop = true
     }
    },
    namer(){
      this.b=false
    },
    fouce(){
      if(this.phone==''){
             this.$toast('请输入电话号码')

      this.ddw=2
      }
 if (!(/^1[3456789]\d{9}$/.test(this.phone))) {
             this.$toast('电话号码格式错误')
      this.ddw=2
           }
    },
    phoner(){
          
