     }
    },
    submit(){
        let datRa={
        account:this.account,
      }
    
      let data={
        account:this.account,
        password:this.password,
        code:this.code
   
      }
      login(data).then(res=>{
        localStorage.setItem('token',res.token)
         console.log(res,1)
        if(res.token){
      //       Toast({
      //   message: res.msg,
      //   duration: 3 * 1000,
      // });
      this.account=''
      this.password=''

        isSubbranch(datRa).then(res=>{
        //  console.log(res)
        if(res.is_sub_branch==0){
        this.$router.push({ name: 'Mall' });
        }else if(res.is_sub_branch==1){
          this.qitashow = true;
           localStorage.setItem('remem',1)
        }

      })

        }else{
               Toast({
        message: res.msg,
        duration: 3 * 1000,
      });
        }
      })
