            this.loan= this.jear[this.je].name
            this.use_loan= this.ytar[this.yt].name
 
      this.integralShow = false;

    },
          delWallet(id){
    
           this.id=id
          
        
      },
       endTimeChange(e) {
         console.log(e)
        let endTimeArr = e.getValues();
        this.end_time = `${endTimeArr[0]}-${endTimeArr[1]}-${endTimeArr[2]}  `
      },
        onSelect(item) {
      this.typename = item.name;
      this.show = false;
    },
     onSelect1(item) {
      this.istype = item.name;
      if(this.istype=='否'){
          this.is_urgency=0

      }else if(this.istype=='是'){
          this.is_urgency=1
      }
      this.show1 = false;
    },
         onSelect2(item) {
      this.typesname = item.name;
      if(this.typesname=='存款'){
        this.type=1
      this.cunkuanshow = true;
      }else if(this.typesname=='取款'){
        this.type=2
      this.qukuanshow = true;
      }else if(this.typesname=='贷款'){
        this.type=3
      this.integralShow = true;
      }else if(this.typesname=='电子银行'){
        this.type=4
      this.dzshow = true;
      }else if(this.typesname=='其他'){
        this.type=5
      this.qitashow = true;
      }
     this.list.push(this.type)
     this.list=Array.from(new Set(this.list))
      this.show2 = false;
    },
            onSelect3(item) {
      if(item.name=='开通手机银行'){
        this.bank_type=1
      }else if(item.name=='开通网上银行'){
        this.bank_type=2
      }else if(item.name=='变更手机号'){
        this.bank_type=3
      }else if(item.name=='重置登录密码'){
        this.bank_type=4
      }else if(item.name=='吞卡处理'){
        this.bank_type=5
      }
      this.integral = false;
    },
    showtime(){
          this.showtime=true
      },
       showweek(){
         if(this.abcd==1){
          this.show=true
         this.adg=1
         }
      },
    //   onSelect(item) {
    //   // 默认情况下点击选项时不会自动收起
    //   // 可以通过 close-on-click-action 属性开启自动收起
    //   this.show = false;
    //   Toast(item.name);
    // },
 go(res) {
      this.$router.push({ name: res });
    },
    goPay() {
      this.$router.push({
        path: "payOrder",
        query: {
          type: 1,
        }
      });
    }
  }
};
</script>
